body, html{
  margin: 0;
  padding: 0;
}

body{
  width: 960px;
  margin: 0 auto;
  font-size: 16px;
}

h1{
  text-align: center;
}

.mainNav ul{
  width: 900px;
  margin: 0 auto;
}

.mainNav ul li {
  width: 80px;
  display: inline-block;
}

.container{
  margin: 30px 0;
}

.game{
  position: relative;
  margin: 0 auto;
  width: 500px;
  height: 500px;
  border: 2px solid rgba(0, 0, 0, 0.75);
  background: rgba(143, 204, 255, 0.68);
}

.game .ball{
  position: absolute;
}
.wall{
  position: absolute;
  background: brown;
}